import React, { Component } from "react";
import scriptLoader from "react-async-script-loader";
import { jskey, webServiceKey, defaultCenter } from "../data";

// IP
// const IP = "113.230.121.67"; // 
// const IP = "119.188.132.10"; // 

class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      map: null,
      SimpleMarker: null,
      markers: {},
      infoWindow: null,
    };
  }

  /**
   * 
   *
   * @param {object} place
   * @returns
   * @memberof Map
   */
  createMarker(place) {
    let marker = new window.AMap.Marker({
      position: place.location.split(","),
      title: place.name
    });
    marker.place = place;
    let { infoWindow, map } = this.state;
    marker.on("click", event => {
      this.popupInfoWindow(marker, infoWindow, map);
    });
    return marker;
  }

  /**
   * 
   *
   * @param {object} marker
   * @param {object} infoWindow
   * @param {object} map
   * @memberof Map
   */
  popupInfoWindow(marker, infoWindow, map) {
    let name = `<p> ${marker.place.name}</p >`;
    let address = `<p>${marker.place.address}</p>`;
    let distance = `<p>${marker.place.distance}</p>`;
    let tel = !Array.isArray(marker.place.tel)
      ? `<p>${marker.place.tel}</p>`
      : "";
    let img =
      marker.place.photos && marker.place.photos[0]
        ? `<img src="${marker.place.photos[0].url}" alt="${
            marker.place.name
          }" />`
        : "";
    let rating = !Array.isArray(marker.place.biz_ext.rating)
      ? `<p>${marker.place.biz_ext.rating}</p>`
      : "";
    let content = `<div class="pop-item">
        ${name}
        ${address}
        ${distance}
        ${tel}
        ${rating}
        ${img}
      </div >`;
    infoWindow.setContent(content);
    // 
    map.setCenter(marker.getPosition());
    // 
    marker.setAnimation("AMAP_ANIMATION_BOUNCE");
    setTimeout(() => {
      marker.setAnimation(null);
    }, 2500);
    // 
    infoWindow.open(map, marker.getPosition());
  }

  /**
   * 
   *
   * @param {array} pos
   * @param {string} label
   * @param {string} style
   * @param {string} title
   * @returns
   * @memberof Map
   */
  createSimpleMarker(pos, label, style, title) {
    let simpleMarker = new this.state.SimpleMarker({
      draggable: false, // 
      iconLabel: {
        innerHTML: label,
        style: {
          color: "#fff"
        }
      },
      iconStyle: style,
      map: this.state.map,
      position: pos,
      title: title
    });

    return simpleMarker;
  }

  /**
   * 
   *
   * @memberof Map
   */
  markCenter(center) {
    this.setState({
      myPosMarker: this.createSimpleMarker(center, "", "red", "")
    });
  }

  /**
   * 
   *
   * @param {array} places
   * @memberof Map
   */
  createMarkers(places) {
    this.cleanMarkers();
    let m = {};
    places.forEach(place => {
      m[place.id] = this.createMarker(place);
    });
    this.setState({ markers: m });
    this.state.map.add(Object.values(m));
    this.state.map.setFitView();
  }

  /**
   * 
   *
   * @memberof Map
   */
  cleanMarkers() {
    if (this.state.map) {
      this.state.map.remove(Object.values(this.state.markers));
    }
  }

  /**
   * IP
   *
   * @param {string} IP IP
   * @memberof App
   */
  getCityByIP(IP) {
    const IP_str = IP ? `&ip=${IP}` : "";
    let url = `https://restapi.amap.com/v3/ip?key=${webServiceKey}${IP_str}`;
    console.log(url);
    fetch(url)
      .then(res => res.json())
      .then(data => {
        let city = data.city;
        this.getCityCenter(city);
      })
      .catch(err => {
        // IP
        this.initMap(defaultCenter);
      });
  }

  /**
   * 
   *
   * @param {string} city 
   * @memberof App
   */
  getCityCenter(city) {
    // 
    fetch(
      `https://restapi.amap.com/v3/config/district?keywords=${city}&key=${webServiceKey}&subdistrict=0&extensions=base`
    )
      .then(response => {
        return response.json();
      })
      .then(data => {
        let center = data.districts[0].center.split(",");
        console.log("city center:", center);
        this.initMap(center);
      })
      .catch(err => {
        // App
        this.initMap(defaultCenter);
      });
  }

  /**
   * 
   *
   * @param {array} center 
   * @memberof Map
   */
  initMap(center) {
    let map = new window.AMap.Map("map", {
      resizeEnable: true,
      zoom: 18,
    });
    let infoWindow = new window.AMap.InfoWindow({
      offset: new window.AMap.Pixel(0, -20)
    });
    this.setState({ map, infoWindow });

    this.props.onMapInited(center);

    window.AMapUI.loadUI(["overlay/SimpleMarker"], SimpleMarker => {
      this.setState({ SimpleMarker });
      this.markCenter(center);
      this.state.map.setFitView();
    });
  }

  /**
   * 
   *
   * @param {*} event
   * @memberof Map
   */
  toggleSidebar(event) {
    this.props.onToggleSidebar();
  }

  componentWillReceiveProps({
    isScriptLoaded,
    isScriptLoadSucceed,
    places,
    clickedPlace
  }) {
    if (isScriptLoaded && !this.props.isScriptLoaded) {
      // load finished
      if (isScriptLoadSucceed) {
        // 
        this.getCityByIP();
      } else this.props.onError();
    }
    // props
    if (this.state.map) {
      // InfoWindow
      if (this.state.infoWindow) {
        this.state.infoWindow.close();
      }
      // PlaceListinfoWindow
      if (clickedPlace) {
        let { markers, infoWindow, map } = this.state;
        let marker = markers[clickedPlace];
        this.popupInfoWindow(marker, infoWindow, map);
        return; // markers
      }
      // markers
      this.createMarkers(places);
    }
  }

  render() {
    return (
      <div className="map-container" id="map" role="application">
        <i
          role="button"
          className="iconfont icon-cebianlan btn-sidebar"
          onClick={event => this.toggleSidebar(event)}
        />
      </div>
    );
  }
}

export default scriptLoader(
  `https://webapi.amap.com/maps?v=1.4.7&key=${jskey}`,
  `https://webapi.amap.com/ui/1.0/main.js`
)(Map);



